.swiper {
  height: 100vh;
}
.img {
  object-fit: cover;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 16px;
}
.text {
  text-align: center;
  position: relative;
  z-index: 2;
  width: 100%;
  z-index: 0;
  margin-top: auto;
  padding: 16px;
}
.text::after {
  position: absolute;
  content: "";
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: black;
  opacity: 0.5;
  z-index: 1;
  border-top-right-radius: 16px;
  border-top-left-radius: 16px;
}

.text p {
  position: relative;
  z-index: 9;
  font-size: 24px;
  line-height: 1.2;
  color: white;
  margin: 0;
}
.slide {
  position: relative;
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  align-items: center;
}
